<template>
  <div id="step-3" class="animate__animated animate__backInUp" v-loading="loading" element-loading-text="正在上传客户照片..." element-loading-background="rgba(243, 241, 235, 0.5)" element-loading-spinner="el-icon-loading">
    <br />
    <div style="
        color: #822E84;
        font-size: 15px;
        border: 1px solid;
        border-radius: 15px;
        padding: 5px 10px;
        text-align: center;
      ">
      数字藏品登记 ➡ 上传客户照片
    </div>
    <br />
    <br />
    <br />
    <el-upload action="#" list-type="picture-card" :auto-upload="false">
      <i slot="default" class="el-icon-plus"></i>
      <div slot="file" slot-scope="{ file }">
        <img class="el-upload-list__item-thumbnail" :src="file.url" alt="" />
        <span class="el-upload-list__item-actions">
          <span class="el-upload-list__item-preview" @click="handlePictureCardPreview(file)">
            <i class="el-icon-zoom-in"></i>
          </span>
          <span v-if="!disabled" class="el-upload-list__item-delete" @click="handleDownload(file)">
            <i class="el-icon-download"></i>
          </span>
          <span v-if="!disabled" class="el-upload-list__item-delete" @click="handleRemove(file)">
            <i class="el-icon-delete"></i>
          </span>
        </span>
      </div>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="" />
    </el-dialog>
    <p style="color:#822E84">注意：上传的照片，不能超过 2M 大小。</p>
    <br />
    <br />
    <br />
    <br />
    <v-btn class="white--text" block color="#822E84" @click="nextStep">
      下一步
    </v-btn>
  </div>
</template>

<script>
export default {
  data: () => ({
    loading: false,
    dialogImageUrl: "",
    dialogVisible: false,
    disabled: false,
  }),
  methods: {
    nextStep() {
      this.loading = true;
      setTimeout(() => {
        this.$router.push("https://uinika.github.io/NFT1/#/bind/step-5");
        this.loading = false;
      }, 2000);
    },
    handleRemove(file) {
      console.log(file);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleDownload(file) {
      console.log(file);
    },
  },
};
</script>

<style lang="scss" scoped>
#step-3 {
  margin: auto 6vw;
}

.el-upload-list--picture-card {
  display: block !important;
}
</style>
